:root {
  --clr-bg-body: #e8e8e8;
  --clr-text-body: #e8f0f0;
  --clr-title: #b8c8c8;
  --clr-bg-header: #102028;
  --clr-text-header: #e8e8e8;
  --clr-social-1: #182830;
  --clr-social-2: #b8c8c8;
  --clr-social-3: #e8e8e8;
  --clr-bg-gallery: #414141;
  --clr-text-gallery: #b8c8c8;
  --clr-h2-gallery: #102028;
  --clr-bg-footer: #102028;
  --clr-bg-footer-2: #182830;
  --clr-text-footer: #e8e8e8;
}

.dark-theme {
  --clr-bg-body: #1a1c22;
  --clr-text-body: #2e2e2e;
  --clr-title: #b8c8c8;
  --clr-bg-header: #17191f;
  --clr-text-header: #e8e8e8;
  --clr-social-1: #1a1c22;
  --clr-social-2: #b8c8c8;
  --clr-social-3: #e8e8e8;
  --clr-bg-gallery: #121212;
  --clr-text-gallery: #b8c8c8;
  --clr-h2-gallery: #b8c8c8;
  --clr-bg-footer: #17191f;
  --clr-bg-footer-2: #1a1c22;
  --clr-text-footer: #e8e8e8;
}

.container, .site-page {
  width: min(100% - 4rem, 80em);
  margin-inline: auto;
}

@keyframes move-in {
  0% {
    transform: translateX(100vw);
  }
  100% {
    transform: translateX(0);
  }
}
.move-in {
  animation: move-in 0.5s ease-in-out forwards;
}

@keyframes move-out {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(100vw);
  }
}
.move-out {
  animation: move-out 0.5s ease-in-out forwards;
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
figure,
blockquote,
dl,
dd,
ul {
  margin: 0;
  padding: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
body {
  font-family: "Nunito", sans-serif;
  color: var(--clr-text-body);
  background-color: var(--clr-bg-body);
}

h1 {
  color: var(--clr-title);
}

.site-page {
  margin-top: 3rem;
  margin-bottom: 5rem;
  min-height: 10vh;
}
@media (max-width: 60em) {
  .site-page {
    margin-block: 3rem;
  }
}

header {
  position: relative;
  height: 70vh;
  background-color: var(--clr-bg-header);
  background-size: cover;
  background-attachment: fixed;
}
@media (max-width: 60em) {
  header {
    height: 50vh;
  }
}
header .topbar {
  padding-block: 1rem;
}
header .topbar .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}
header .topbar .container .topbar-logo {
  text-decoration: none;
}
header .topbar .container .topbar-logo h1 {
  color: var(--clr-text-header);
  font-size: 1.5rem;
}
header .topbar .container .topbar-right {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  gap: 1rem;
}
header .topbar .container .topbar-right .social-button {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  text-decoration: none;
  background-color: var(--clr-social-1);
  border-radius: 50%;
  height: 30px;
  width: 30px;
  transition: all 0.7s ease;
}
header .topbar .container .topbar-right .social-button i {
  color: var(--clr-social-3);
}
header .topbar .container .topbar-right .social-button:hover {
  background-color: var(--clr-social-2);
}
header .topbar .container .topbar-right .social-button:hover i {
  color: var(--clr-social-1);
}
header .topbar .container .topbar-right .theme-mode-toggle .checkbox {
  opacity: 0;
  position: absolute;
}
header .topbar .container .topbar-right .theme-mode-toggle .checkbox:checked + .label .ball {
  transform: translateX(20px);
}
header .topbar .container .topbar-right .theme-mode-toggle .label {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  width: 54px;
  height: 30px;
  padding: 0.125rem 0.5rem;
  background-color: var(--clr-social-1);
  border-radius: 50px;
  position: relative;
}
header .topbar .container .topbar-right .theme-mode-toggle .label i {
  font-size: 15px;
}
header .topbar .container .topbar-right .theme-mode-toggle .label .ball {
  width: 18px;
  height: 18px;
  background-color: var(--clr-text-header);
  position: absolute;
  border-radius: 50%;
  transition: transform 0.2s linear;
  cursor: pointer;
}
header .banner {
  display: grid;
  place-items: center;
  width: 100%;
  height: calc(100% - 225px);
}
@media (max-width: 60em) {
  header .banner {
    height: calc(100% - 118px);
  }
}
header .banner__content {
  width: min(100% - 5rem, 500px);
  margin-inline: auto;
}
header .banner__content--text {
  padding-bottom: 1rem;
  text-align: center;
}
header .banner__content--text p {
  color: var(--clr-title);
  font-size: 1.25rem;
}
header .banner__content--search {
  position: relative;
}
header .banner__content--search input {
  width: 100%;
  border: none;
  border-radius: 3rem;
  height: 3rem;
  padding-inline: 3rem;
}
header .banner__content--search input:focus {
  border: none;
}
header .banner__content--search .fa-magnifying-glass {
  position: absolute;
  top: 30%;
  color: var(--clr-bg-header);
  left: 1rem;
}
header .banner__content--search .fa-xmark {
  position: absolute;
  top: 33%;
  color: var(--clr-bg-header);
  right: 2rem;
  cursor: pointer;
}
header .banner__content--error {
  color: red;
}
header .wave {
  position: absolute;
  bottom: 0;
  width: 100%;
  overflow: hidden;
  height: 150px;
}
@media (max-width: 39.9375em) {
  header .wave {
    height: 50px;
  }
}

.gallery h2 {
  color: var(--clr-h2-gallery);
}
.gallery__container {
  display: grid;
  grid-template-columns: repeat(4, 300px);
  gap: 1rem;
  place-content: center;
}
@media (max-width: 87.4375em) {
  .gallery__container {
    grid-template-columns: repeat(3, 300px);
  }
}
@media (max-width: 71.8125em) {
  .gallery__container {
    grid-template-columns: repeat(3, 250px);
  }
}
@media (max-width: 60em) {
  .gallery__container {
    grid-template-columns: repeat(2, 300px);
  }
}
@media (max-width: 39.9375em) {
  .gallery__container {
    grid-template-columns: 300px;
  }
}
.gallery__item {
  height: 300px;
  width: 300px;
  border-radius: 0.25rem;
  overflow: hidden;
  background-color: var(--clr-bg-gallery);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
@media (max-width: 71.8125em) {
  .gallery__item {
    height: 250px;
    width: 250px;
  }
}
@media (max-width: 60em) {
  .gallery__item {
    height: 300px;
    width: 300px;
  }
}
.gallery__item:hover {
  cursor: pointer;
}
.gallery__item h2 {
  color: var(--clr-text-gallery);
}
.gallery__item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.footer {
  color: var(--clr-text-footer);
  background-color: var(--clr-bg-footer);
}
.footer__content {
  background-color: var(--clr-bg-footer);
  height: 30vh;
}
.footer__copyright {
  background-color: var(--clr-bg-footer-2);
  color: var(--clr-title);
  padding-block: 1rem;
  display: grid;
  place-content: center;
}
.footer__copyright--text {
  font-size: 0.85rem;
  text-align: center;
}